@tailwind base;
@tailwind components;
@tailwind utilities;

@media (min-width: 1024px) {
  footer {
    position: absolute;
    bottom: 0;
  }
}
.title {
  font-family: "Stick No Bills", sans-serif;
}

.svg > div > svg {
  fill: white;
}

.svg > div > svg:hover,
.title:hover {
  animation: fade 2s infinite;
}

@keyframes fade {
  from {
    fill: white;
    color: white;
  }
  to {
    fill: red;
    color: red;
  }
}

footer {
  height: 100px;
  position: relative;
  bottom: 0;
}

.grid-pic {
  max-width: 70%;
  margin: auto;
}

.video-container .video {
  height: 15rem;
  width: 25rem;
  margin: 2rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  border-radius: 1rem;
}

.video-container .video iframe {
  height: 100%;
  width: 100%;
  object-fit: cover;
  outline: none;
  border: none;
  cursor: pointer;
}

.video-container .video iframe:hover {
  transition: 0.2s linear;
  transform: scale(1.05);
}

.video-container .video .active {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: auto;
  width: 80%;
  box-shadow: 0 0 0 100vh rgba(0, 0, 0, 0.7);
  z-index: 1;
}

.video-container .video .active:hover {
  transition: none;
  transform: translate(-50%, -50%) scale(1);
}
